.App {
  text-align: center;
  font-family: Arial, sans-serif;
  margin-top: 0px;
  display: flex;
  width: 100%;
  height: 100%;
}

@media only screen and (min-width: 768px) {
  .App {
    width: 50%;
    min-height: 50vh;
    height: 50vh;
  }
}
@media only screen and (min-width: 992px) {
  .App {
    width: 50%;
  }
}


button:hover {
  background: #0056b3;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.App::-webkit-scrollbar {
  display: none;
}

